/////// for images in the EXAMPLE SECTION ///////
var imgNum = 6;

function setImg(id) {
    // console.log("setting image");
    var styles = {
        'background': 'url(./images/examples/img_' + id + '.jpg) no-repeat center center',
        '-webkit-background-size': 'contain',
        '-moz-background-size': 'contain',
        '-o-background-size': 'contain',
        'background-size': 'contain',
    };
    $("#img_" + id).css(styles);
}

var ind;
for (ind = 1; ind < imgNum + 1; ind++) {
    setImg(ind);
}

function initDom() {
    //Eric code
    var profile = $('.container.testimonials .test-controller').find(".profile");
    var content = $('.container.testimonials').find(".content");
    profile.each(function (index) {
        $(this).click(function () {
            $(this).addClass('selected').siblings().removeClass('selected');
            content.removeClass('active').eq(index).addClass('active');
        })
    });

    var viewOpt = $('.container.app .view-selector').find(".view-opt");
    var appContent = $('.container.app').find(".app-content");
    viewOpt.each(function (index) {
        $(this).click(function () {
            $(this).addClass('selected').siblings().removeClass('selected').find('img').addClass('gray');
            appContent.removeClass('active').eq(index).addClass('active');
            $(this).find('img').removeClass('gray');
        })
    });

    var tabs = $('.container.app').find('.tab');
    tabs.each(function (index) {
        $(this).click(function () {
            $(this).addClass('selected').siblings().removeClass('selected');
            var $list = $('.container.app').find('.discovery-list');
            $list.find('.discovery-li').hide().eq(index).show();
        })
    });
}

initDom();

$(document).on("switch-lang",function () {
    // initDom();
});

//end Eric code
var about_pos = $(".about").position().top;
var footer_pos = $(".footer").position().top;
var offset = 16;
// $(".about").scroll(function() {
//   console.log("scrolling");
// })

$("body").scroll(function (e) {
    // console.log("scrolling");
    // console.log($("body").scrollTop());
    var curr_pos = $("body").scrollTop();
    if (curr_pos >= (about_pos - offset * 10)) {
        $(".about .svg-earnie").css("display", "initial");
    }
    if (curr_pos >= (footer_pos - offset * 10)) {
        $(".footer .footer-earnie").css("display", "initial");
    }
});






